body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    background: gray;
}

.Portfolio {
    position: relative;
    float: left;
    height: 30%;
    width: 40%;
    margin: 20px;
    padding: 40px;
    background: #fff;

    border: 2px solid black;
}



.Stocks {
    background: #aaa;
    
    height: 200px;
    overflow: auto;
    border: 2px solid black;
}

.stocksTable {
    width: 100%;
}


#addPortfolio {
    display: none;
}
#addStock {
    display: none;
}

#newPortfolio button {
    width: 190px;
    height: 40px;
}

#newPortfolio {
    text-align: center;
    background-color: #aaa;
    border-bottom: 1px solid black;
    padding: 10px;
}

#newPortfolio label{
    font-size: 18px;
    padding-right: 10px;
}


.addStockForm {
    background-color: antiquewhite;
    border: 2px solid black;
    padding: 10px;
    margin: 10px;
}


#removeButton {
    background-color: #f08080;
    float: right;
}


#tableHead {
    color: white;
    background-color: #000000;
}

#tableBody {
    text-align: center;
    background-color: white;
    border: 1px solid black;

}
